:root {
  --color-primary: #f07900;
  --color-white: #eef1ef;
  --color-white-fade: #eef1ef20;
  --color-black: #111;
  --color-grey-100: #2b2b2b;
  --color-grey-200: #242424;
  --color-pink-100: #dcc6e0;
  --font: -apple-system, BlinkMacSystemFont, "Noto Sans", sans-serif;
  --radius: 6px;
}

body {
  background-color: var(--color-black);
  font-family: var(--font);
  color: var(--color-white);
  margin: 0;
  padding: 0 0 50px;
  font-size: 14px;
  line-height: 1.5;
}

h1 {
  font-size: 38px;
  font-weight: 900;
  line-height: 1;
}

hr {
  border-color: var(--color-grey-100);
  border-style: solid;
  margin: 2em 0;
}

* {
  box-sizing: border-box;
}

a {
  color: var(--color-primary);
  text-decoration: none;
}

button {
  background-color: var(--color-primary);
  border: 0;
  border: 2px solid var(--color-primary);
  color: var(--color-grey-100);
  cursor: pointer;
  border-radius: 6px;
  padding: 4px 8px;
  font-size: 14px;
}

ul {
  padding-left: 12px;
}

.recipe {
  max-width: 400px;
  margin: 0 auto;
  padding: 128px 12px 0;
}

.recipe h1 {
  margin-top: 0;
}

@media (width <= 550px) {
  .recipe {
    padding-top: 68px;
  }
}

.go-back {
  margin-bottom: 8px;
  display: inline-block;
}

.footer {
  border-radius: var(--radius);
  background-color: var(--color-black);
  padding: 4px 6px;
  position: fixed;
  bottom: 14px;
  left: 50%;
  transform: translateX(-50%);
}

